footer ul {

    display: flex;
    justify-content: space-around;
    margin: 0;
    padding: 5.5vh 0;
}

footer ul li a {

    color: #fff;
    transition: .3s linear color;
    position: relative;
    font-weight: 600;
}

footer ul li a:hover {

    color: rgb(192,192,192) ; 
}

footer ul li a:hover::before , footer ul li a:hover::after {

    content: '' ;

    position: absolute;

    width: 0;

    height: 2px;
    left: 0;
    top: 100%;
    background: goldenrod;

    animation: underlineMiddle .35s linear forwards;
}

footer ul li a:hover::after {

    top: 0%!important;
}

